

@font-face {
	font-family: "Raleway-black";
	src: url('../fonts/raleway-black-webfont.woff');
}

@font-face {
	font-family: "Raleway-bold";
	src: url('../fonts/raleway-bold-webfont.woff');
}

@font-face {
	font-family: "Pacifico";
	src: url('../fonts/pacifico-webfont.woff');

}




$zmienna : rgb(173, 230, 117);
$font: 'Raleway',sans-serif;
$zmienna1: rgb(35, 163, 14);
$zmienna2: rgb(190, 130, 82);






/* komentarz*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 16px;
	font: inherit;
	vertical-align: baseline;
  box-sizing: border-box;
}

body {
    background:rgb(255, 255, 255);
    height: 100vh;
		//padding-top: 70px;
}

.navbar-toggle {
    position:relative;
    float:right;
    padding:10px 10px;
    margin-top:15px;
    margin-right:15px;
    margin-bottom:8px;
    background-color:transparent;
    background-image:none;
  	border-radius:4px;
	border:transparent;
}

.icon-bar {
    display:block;
    width:30px;
    height:1px;
		background-color: rgb(29, 29, 27);
    border-radius:5px;
		border:1px solid #ffffff;
		padding: 2px;
		//color:rgb(46, 47, 47);

}

h1 {
  color: $zmienna1;
}

.logo {
	display: block;
	//padding-right: 400px;
  font-family: 'Pacifico',cursive;
  font-size: 300%;
  margin-top: 25px;
	margin-left: 7vw;
  font-weight: 400;
  float: left;
  color: #262626;
}

.navbar {
	display:none;
	position: fixed;
	margin: 0 auto;
	text-align: center;
	z-index: 500;
}

#navigation-bar 	 {
  height: 147px;
	width: 100vw;
  border: 1px solid red;
	background-color: #f9f8fb;
	position: fixed;
	margin: 0 auto;
	text-align: center;
	z-index: 500;
	//display: inline-block;
	//list-style-type: none;

a {
	color: rgb(5, 5, 5);
	font-family: $font;
	font-weight: bold;
	padding:9px;
	cursor: pointer;

	///padding-top: 3px;
}
a:hover {
	border-top: 3px solid #ff6033;
	text-decoration: none;
}
}

.menu {
	display: inline;
	float: right;
	margin-right: 7vw;
}

.container {
  width: 100vw;
  border: 1px solid red;
  //background: rgb(189, 19, 128);
}

#home {
  background-image: url('../img/home.png');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: right;
  height: 100vh;
  margin: 0 auto;
  border: 1px solid green;
h2 {
	font-family: "Raleway-black";
	font-size: 40px;
	margin-top: 180px;
}
p {
	margin: 20px 0 20px 0;
}
}
.btn {
	display: inline-block;
	height: 40px;
	width: 160px;
	background-color: rgb(235, 108, 4);
	padding: 6px 12px;
	color: white;
	font-size: 17px;
	font-family: "Raleway";
	vertical-align: middle;
	box-shadow: 0 2px 5px 0 #000000;
}
.scroll-image {
	display: block;
	position: absolute;
	top: 90%;
	left: 50%;
	width: 40px;
	height: 70px;

}

#about {
  background: #00bbbb;
	background-image: url('../img/strawberry.png');
	background-repeat: no-repeat;
	background-position: 25% 25%;
  height: 700px;
}

.about-content{
	display: flex;
	color: rgb(255, 255, 255);
	h2 {
		font-size: 3em;
		font-family: "Raleway-bold";

	}
}

#services {
  background: #70bfff;
  height: 790px;
	background-image: url('../img/services.png');
	background-repeat: no-repeat;
	background-position: 75% 25%;
}

#gallery {
  background: #ffe92f;
  height: 1536px;
	text-align: center;
h2 {
	color: rgb(16, 17, 16);
	font-family: "Raleway-bold";
	font-size: 3em;
	}
p {
	margin: 20px 0 20px 0;

}
}

#subscribe {
  background: #ffffff;
  height: 500px;
}

#blog {
  background: #b07cf7;
  height: 863px;
}

#contact img {
    width:  100vw;
}
#contact {
  //background: #7cf781;
  height: 866px;
	background-position: center;
}

footer {
  height: 90px;
  background: #262626;
}

ul li {
	display: inline-flex;
	flex-flow: row wrap;
	align-items: flex-end;
	//float:right;
	margin-top: 55px;
	color: $zmienna1;
	padding-right: 2%;
	font-family: "Raleway-bold";
	color: #262626;
}
.col-centered {
	float:none;
	margin: 0 auto;
	margin-top: 100px;
}
